body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Open Sans", sans-serif;
  background-color: #002e38;
  color: #ffffff;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  color: #ffffff;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

/* Swiper */
.swiper-pagination-bullets {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 27px;
}

.swiper-pagination-bullet {
  position: relative;
  width: 15px;
  height: 15px;
  background-color: #ffffff;
  border: 3px solid #189b80;
  border-radius: 50%;
  box-sizing: border-box;
}

.swiper-pagination-bullet-active {
  background-color: transparent;
  border-color: #ed344a;
}

/* Blocks */
.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 15px 25px;
  background-color: #ed344a;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 15px;
  line-height: 1.2;
  cursor: pointer;
}
.button::after {
  content: "";
  position: absolute;
  top: -20px;
  right: -20px;
  display: block;
  width: 20px;
  height: 20px;
  background-color: inherit;
  transition: 0.3s ease;
}
.button:hover::after {
  transform: translate(-20px, 20px);
}

