.top-event__image {
  max-width: 403px;
  width: 100%;
}
.top-event__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.top-event__content {
  padding: 49px 10px 57px 49px;
}
.top-event__type {
  font-size: 11px;
  line-height: 1.36;
}
.top-event__date {
  font-size: 25px;
  line-height: 1.25;
  margin-top: 3px;
}
.top-event__title {
  font-family: "Tektur", sans-serif;
  font-size: 30px;
  line-height: 1.1;
  margin-top: 30px;
}
.top-event__title span {
  color: #00ffb0;
}
.top-event__link {
  color: #00ffb0;
  font-family: "Lato", sans-serif;
  font-size: 15px;
  line-height: 1.2;
  margin-top: 38px;
  text-decoration: underline;
}

.title {
  font-family: "Tektur", sans-serif;
  font-size: 37px;
  line-height: 1;
  text-transform: uppercase;
}
.title span {
  color: #00ffb0;
  padding-left: 67px;
}